@import '../../../../../assets/scss/color';
@import '../../../../../assets/scss/fonts';





























































































































































































































































































































































































































































































































































































































































































.right-toolbar {
  .el-collapse-item {
    .el-collapse-item__header {
      border: none;
      background: #f0f3fa;
      height: 40px;
      padding: 0 20px;
      border-bottom: 1px solid #e1e4e8;
      font-size: 14px;
      font-weight: 600;
      color: #233a72;
      .el-icon-arrow-right {
        margin: 0 0 0 auto;
        &:before {
          content: '\e791';
          color: #bfc3cc;
        }
      }
    }
    .el-collapse-item__wrap {
      border-bottom: none;
      .el-collapse-item__content {
        padding: 10px 20px;
      }
    }
    &.is-active .el-collapse-item__header,
    &:last-child .el-collapse-item__header {
      border-bottom-color: transparent;
    }
  }
  .el-form-item__label {
    font-size: 14px;
    color: #233a72;
    padding: 0;
  }
  .el-form-item {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-input__inner {
    border-color: #e1e4eb;
    border-radius: 2px;
  }
  .el-select {
    width: 100%;
  }
  .fields-tree {
    border: 1px solid #e1e4eb;
    border-radius: 2px;
    height: 220px;
    overflow: auto;
    padding: 10px 14px;
    margin-top: 10px;
    &::-webkit-scrollbar {
      width: 4px;
    }
    .el-tree-node__label {
      font-size: 14px;
      line-height: 20px;
      color: #162a5a;
    }
    .el-tree-node__children .el-tree-node__label {
      color: #596580;
    }
    .el-icon-caret-right:not(.is-leaf) {
      color: #bfc3cc;
    }
  }
  .el-date-editor.el-input {
    width: 100%;
  }
}
.requiredItem .el-form-item__label {
  position: relative;
  &:before {
    position: absolute;
    left: -10px;
  }
}
