@import '../../../../../assets/scss/color';
@import '../../../../../assets/scss/fonts';


































































































































.layoutDialog {
  .el-dialog__body {
    background: rgba(139, 138, 138, 0.8);
    overflow: hidden;
    padding-bottom: 0;
    .grid-layout {
      display: grid;
      grid-template-columns: repeat(3, 33.33%);
      background: #fff;
      box-sizing: border-box;
      width: calc(50% - 10px);
      float: left;
      cursor: pointer;
      position: relative;
      margin-bottom: 20px;
      &:nth-child(2n) {
        margin-left: 20px;
      }
      &.active {
        box-shadow: 0 0 8px #266ab9;
      }
      &:hover {
        box-shadow: 0 0 8px #266ab9;
      }
      .delete-layout {
        position: absolute;
        right: 10px;
        top: 10px;
        .svg-icon {
          width: 24px;
          height: 24px;
        }
      }
      .grid-item {
        box-sizing: border-box;
        &.rightBorder {
          border-right: 1px solid #e1e4e8;
        }
        &.bottomBorder {
          border-bottom: 1px solid #e1e4e8;
        }
      }
    }
  }
}
