@import '../../../../../assets/scss/color';
@import '../../../../../assets/scss/fonts';





























































































































































































































.left-toolbar {
  position: absolute; // 使用absolute，如果使用fixed会在页面进入和退出时错位
  top: 0;
  bottom: 0;
  left: 0;
  width: 260px;
  box-shadow: 0 2px 8px 0 rgba(15, 30, 66, 0.1);
  background: #fff;
  border-radius: 2px 0 0 2px;
  transition: 0.1s width;
  &.narrow {
    width: 140px;
    .toolbar-box .dragItem:nth-child(even) {
      margin-right: 0;
    }
    .toolbar-box.title .el-input {
      width: 100px;
    }
  }
  .toolbar-container {
    box-sizing: border-box;
    padding: 20px 0 20px 20px;
    width: 100%;
    height: 100%;
    overflow: auto;
    &::-webkit-scrollbar {
      width: 4px;
    }
  }
  .toolbar-box {
    margin-bottom: 10px;
    font-size: 0;
    &.title {
      margin-bottom: 20px;
      .el-input {
        width: 220px;
      }
    }
    .toolbar-title {
      margin-bottom: 10px;
      font-size: 14px;
      color: #162a5a;
      font-weight: bold;
    }
    .dragItem {
      display: inline-block;
      width: 100px;
      line-height: 28px;
      background: #eff4ff;
      color: #233a72;
      padding-left: 10px;
      box-sizing: border-box;
      font-size: 12px;
      margin-bottom: 10px;
      cursor: move;
      &:nth-child(even) {
        margin-right: 20px;
      }
      .item-icon {
        margin-right: 4px;
      }
    }
  }
  .left-open-btn {
    position: absolute;
    width: 15px;
    height: 41.96px;
    cursor: pointer;
    top: 50%;
    right: 0;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
